.headerContainer{
    background-color: #131A22;
    display: flex;
    align-items: center;
}

.headerDropDownText{
    font-size: 12px;
}

.navbar-toggler-icon{
    font-size: 20px;
    color: white;
}

.hamburgerButton{
    padding: 5px 4px;
    border: 1px thin white;
}

.headerLogo{
    margin: 11px -2px 0px 15px;
    object-fit: contain;
    width: 100px;
}

.headerSearch{
    flex: 1;
}   

.headerSearchInput{
    padding: 21px;
}

input{
    height: 12px;
    padding: 10px;
    width: 100%;
}

.headerSearchIconContainer{
    background-color: #febd69;
}
.headerSearchIcon{
    font-size: 30px;
}

.dropDownToggleBtn{
    vertical-align: bottom;
}

.headerOptions{
    margin-left: 12px;
    margin-right: 12px;
}

.indianFlag{
    width: 24px;
    height: 18px;
    vertical-align: bottom;
}

.headerNav{
    justify-content: space-evenly;
}

.headerOptionLineOne{
    font-size: 12px;    
}

.headerOptionLineTwo{
    font-size: 14px;
    font-weight: 700;
}

.navbarShoppingCount{
    color: #f08804;
    display: flex;
    font-weight: 700;
}

.shoppingCartText{
    vertical-align: bottom;
}

.locationIcon{
    align-items: center;
    font-size: 22px;
    margin-right: 3px;
    color: #ccc;
    margin-top: 8px;
}

.subHeaderLineOne{
    font-size: 12px;
    font-weight: 400;
    color: #ccc;
}

.subHeaderLineTwo{
    font-size: 14px;
    font-weight: 700;
    margin-right: 11px;
    margin-top: -5px;

}

.locationContainer{
    margin-right: 2px;
    padding-right: 9px;
}

@media (max-width: 767px){
    .headerContainer{
        padding: 15px !important;
    }

    .navbar-dark .navbar-brand {
        margin: 0 00 10px 0;
        padding: 0;
    }

    .headerSearch{
        width: 100%;
        flex: 0 0 100%;
    }
}