.homeImage{
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.4));
}


.homeCardTitle{
    float: left;
}

.homeCardText{
    font-size: 21px;
    font-weight: 400;
}

.homeCardLink{
    font-size: 14px;
}

.homeCardLink:hover{
    color: #c45500;
    cursor: pointer;
}

.homeCardsDetail{
    top: -250px;
    position: relative;
    margin-bottom: -250px;
}

.homeCardTitle{
    font-size: 21px;
    text-align: justify;
}

.homeCardContainer{
    max-width: 350px;
}

.cardImgContainer{
    max-width: 310px;
    max-height: 276px;
}

.seeMoreBtnLink{
    float: left;
    padding-left: 0;
}

.carouselImgContainer{
    width: 200px;
    height: 200px;
}

@media (max-width: 767px){
    .homeCardsDetail{
        top: 0px;
        margin-bottom: 0px;
    }
}