.subNavbar{
    background-color: #232F3E;
    display: flex;
    align-items: center;
}

.subHeaderOptions{
    width: calc(75% - 65px);
    font-size: 14px;
}

.subHeaderListItems span{
    padding: 8px 9px;
}

.rightSideImg{
    width: 25%;
}

.subHeaderMenuIconLable{
    font-size: 14px;
    font-weight: 700;
    padding-left: 5px;
}

.menuIcon{
    padding-left: 2px;
    height: 14px;
    font-size: 17px;
}

.menuContainer{
    width: 65px;
    margin-left: 15px;
}

.subHeaderRightImg{
    width: "400px";
    height: '39px';
}

@media (max-width: 767px){
    .menuContainer {
        width: 65px;
    }

    .subHeaderOptions{
        width: calc(100%);
        padding: 8px 0;
    }

    .rightSideImg{
        width: 100%;
    }

    .subHeaderListItems{
        font-size: 12px;
        white-space: nowrap;
        overflow-x: auto;
        margin-left: 15px;
    }

    .subHeaderListItems span{
        padding: 0px 5px;
    }
}